import React from 'react'
import { graphql, Link } from 'gatsby'

const Template = ({data, pageContext}) => {
    
    const { html, frontmatter: { title } } = data.markdownRemark
    const { next, prev } = pageContext
    return (
        <div>
            <h1 style={{ fontFamily: 'avenir' }}>{title}</h1>
            <div className='blogpost'
                style={{ fontFamily: 'avenir' }}
                dangerouslySetInnerHTML={{ __html: html}}
            />
            <div style={{
                display: 'flex',
                flexDirection: 'row',
                alignContent: 'space-between',
                fontFamily: 'avenir',
                width: '100%'
            }}>
                <div style={{marginRight: 'auto'}}>{prev ? <Link to={prev.frontmatter.path}>Prev Post</Link> : <span>Prev Post</span> }</div>
                <div style={{marginRight: 'auto'}}><Link to='/'>Home</Link></div>
                <div style={{marginRight: 'auto'}}>{next ? <Link to={next.frontmatter.path}>Next Post</Link> : <span>Next Post</span> }</div>
            </div>
        </div>
    )
}

export const query = graphql`
    query($pathSlug: String!) {
        markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
            html
            frontmatter {
                title
            }
        }
    }
`

export default Template
